const is_promising = (
  table: Array<Array<boolean | null>>,
  row: Array<Array<number>>,
  column: Array<Array<number>>
) => {
  let i, j, fill_count, x_count, sum;
  const SIZE = table.length;

  // 
  for (i = 0; i < SIZE; i++) {
    fill_count = 0;
    x_count = 0;
    sum = 0;
    for (j = 0; j < SIZE; j++) {
      //  
      if (table[i][j] === true) fill_count++;
      else if (table[i][j] === false) x_count++;
    }

    for (j = 0; j < row[i].length; j++) {
      //  
      sum += row[i][j];
    }

    if (fill_count > sum) return false; //       
    if (x_count > SIZE - sum) return false; //X  (SIZE -   )  
  }

  // 
  for (j = 0; j < SIZE; j++) {
    fill_count = 0;
    x_count = 0;
    sum = 0;
    for (i = 0; i < SIZE; i++) {
      //  
      if (table[i][j] === true) fill_count++;
      else if (table[i][j] === false) x_count++;
    }

    for (i = 0; i < column[j].length; i++) {
      //  
      sum += column[j][i];
    }

    if (fill_count > sum) return false; //       
    if (x_count > SIZE - sum) return false; //X  (SIZE -   )  
  }

  return true; //  
};


// 
const sumAll = (arr: Array<number>) => {
  return arr.reduce((a, b) => a + b) + arr.length - 1;
};

const copyTable = (
  dest: Array<Array<boolean | null>>,
  src: Array<Array<boolean | null>>
) => {
  let h = 0,
    w = 0;
  const SIZE = dest[0].length;

  for (h = 0; h < SIZE; h++) {
    for (w = 0; w < SIZE; w++) {
      dest[h][w] = src[h][w];
    }
  }
};
const isEqualTable = (
  arr1: Array<Array<boolean | null>>,
  arr2: Array<Array<boolean | null>>
) => {
  let h, w;
  const SIZE = arr1.length;
  if (arr2.length !== SIZE) return false;
  for (h = 0; h < SIZE; h++) {
    for (w = 0; w < SIZE; w++) {
      if (arr1[h][w] !== arr2[h][w]) return false;
    }
  }
  return true;
};
const processX = (
  table: Array<Array<boolean | null>>,
  rowConditions: Array<Array<number>>,
  columnConditions: Array<Array<number>>
) => {
  compeleteX(table, rowConditions, columnConditions); //  X    
  sequenceX(table, rowConditions, columnConditions); //         (X )
  XCondition4({ table: table, row: rowConditions, column: columnConditions }); //    X
};

//  X     
const decreaseSize = (
  table: Array<Array<boolean | null>>,
  row: Array<Array<number>>,
  column: Array<Array<number>>
) => {
  let h = 0,
    w = 0,
    i = 0,
    j = 0;
  const SIZE = row.length;
  let ptr = 0,
    front_ptr = 0,
    tail_ptr = 0,
    sum = 0;

  for (w = 0; w < SIZE; w++) {
    //  -> 
    front_ptr = 0;
    for (h = 0; h < SIZE; h++) {
      if (table[h][w] === false) {
        front_ptr++;
      } else {
        break;
      }
    }

    //  -> 
    tail_ptr = 0;
    for (h = SIZE - 1; h >= 0; h--) {
      if (table[h][w] === false) {
        tail_ptr++;
      } else {
        break;
      }
    }

    //    
    sum = 0;
    for (i = 0; i < column[w].length; i++) {
      sum += column[w][i];
    }
    sum += column[w].length - 1;

    //   X       
    if (SIZE - (front_ptr + tail_ptr) === sum) {
      ptr = front_ptr; // ptr 
      //   
      for (i = 0; i < column[w].length; i++) {
        //    
        for (j = 0; j < column[w][i]; j++) {
          table[ptr][w] = true;
          ptr++;
        }
        ptr++;
      }
    }
  }

  for (h = 0; h < SIZE; h++) {
    //  -> 
    front_ptr = 0;
    for (w = 0; w < SIZE; w++) {
      if (table[h][w] === false) {
        front_ptr++;
      } else {
        break;
      }
    }

    //  -> 
    tail_ptr = 0;
    for (w = SIZE - 1; w >= 0; w--) {
      if (table[h][w] === false) {
        tail_ptr++;
      } else {
        break;
      }
    }

    //    
    sum = 0;
    for (i = 0; i < row[h].length; i++) {
      sum += row[h][i];
    }
    sum += row[h].length - 1;

    //   X       
    if (SIZE - (front_ptr + tail_ptr) === sum) {
      ptr = front_ptr; // ptr 
      //   
      for (i = 0; i < row[h].length; i++) {
        //    
        for (j = 0; j < row[h][i]; j++) {
          table[h][ptr] = true;
          ptr++;
        }
        ptr++;
      }
    }
  }
};

const noBaseFunc3 = ({
  SIZE,
  row,
  column,
}: {
  SIZE: number;
  row: Array<Array<number>>;
  column: Array<Array<number>>;
}) => {
  const new_table: Array<Array<boolean | null>> = [];
  let i, j, k;
  let remain, offset;

  for (i = 0; i < SIZE; i++) {
    const new_row = [];
    for (j = 0; j < SIZE; j++) {
      new_row.push(null);
    }
    new_table.push(new_row);
  }

  // 
  for (i = 0; i < row?.length; i++) {
    remain = SIZE - sumAll(row[i]);
    offset = 0; //      

    for (j = 0; j < row[i].length; j++) {
      if (row[i][j] > remain) {
        for (k = offset + remain; k < offset + row[i][j]; k++) {
          new_table[i][k] = true;
        }
      }
      offset += row[i][j] + 1;
    }
  }

  // 
  for (i = 0; i < column.length; i++) {
    remain = SIZE - sumAll(column[i]);
    offset = 0; //      

    for (j = 0; j < column[i].length; j++) {
      if (column[i][j] > remain) {
        //for (k = i; k < i + (column[i][j] - remain); k++) table[k][i] ;
        for (k = offset + remain; k < offset + column[i][j]; k++) {
          new_table[k][i] = true;
        }
      }
      offset += column[i][j] + 1;
    }
  }
  return new_table;
};

//        ([   ] === [  ]     , X )
const fillEnds = (
  table: Array<Array<boolean | null>>,
  row: Array<Array<number>>,
  column: Array<Array<number>>
) => {
  let i, j, min, max;
  let isFilled;
  const SIZE = table.length;

  // 
  for (i = 0; i < SIZE; i++) {
    //    ( -> )
    min = -1;
    max = -1;
    isFilled = false;

    //   
    for (j = 0; j < SIZE; j++) {
      if (table[i][j] !== false) {
        min = j;
        break;
      }
    }
    if (min === -1) continue; //   

    //   
    for (j = min; j < SIZE; j++) {
      if (table[i][j] !== false) {
        max = j;
        if (table[i][j] === true) isFilled = true;
      } else break;
    }
    if (!isFilled) continue; //    

    if (row[i][0] === max - min + 1) {
      // 
      for (j = min; j <= max; j++) {
        table[i][j] = true; // 
      }
    }
  }

  //->
  for (i = 0; i < SIZE; i++) {
    min = -1;
    max = -1;
    isFilled = false;

    //   
    for (j = SIZE - 1; j >= 0; j--) {
      if (table[i][j] !== false) {
        max = j;
        break;
      }
    }
    if (max === -1) continue; //   

    //   
    for (j = max; j >= 0; j--) {
      if (table[i][j] !== false) {
        min = j;
        if (table[i][j] === true) isFilled = true;
      } else break;
    }
    if (!isFilled) continue;

    if (row[i][row[i].length - 1] === max - min + 1) {
      // 
      for (j = min; j <= max; j++) {
        table[i][j] = true; // 
      }
    }
  }

  // 
  for (j = 0; j < SIZE; j++) {
    //    ( -> )
    min = -1;
    max = -1;
    isFilled = false;

    //   
    for (i = 0; i < SIZE; i++) {
      if (table[i][j] !== false) {
        min = i;
        break;
      }
    }
    if (min === -1) continue; //   

    //   
    for (i = min; i < SIZE; i++) {
      if (table[i][j] !== false) {
        max = i;
        if (table[i][j] === true) isFilled = true;
      } else break;
    }
    if (!isFilled) continue;

    if (column[j][0] === max - min + 1) {
      // 
      for (i = min; i <= max; i++) {
        table[i][j] = true; // 
      }
    }
  }

  //->
  for (j = 0; j < SIZE; j++) {
    min = -1;
    max = -1;
    isFilled = false;

    //   
    for (i = SIZE - 1; i >= 0; i--) {
      if (table[i][j] !== false) {
        max = i;
        break;
      }
    }
    if (max === -1) continue; //   

    //   
    for (i = max; i >= 0; i--) {
      if (table[i][j] !== false) {
        min = i;
        if (table[i][j] === true) isFilled = true;
      }
    }
    if (!isFilled) continue;

    if (column[j][column[j].length - 1] === max - min + 1) {
      // 
      for (i = min; i <= max; i++) {
        table[i][j] = true; // 
      }
    }
  }
};
function equal(arr1: Array<number>, arr2: Array<number>): number {
  let i = 0,
    SIZE = 0;

  if (arr1.length === arr2.length) {
    //  
    SIZE = arr1.length;
    for (i = 0; i < SIZE; i++) {
      //  
      if (arr1[i] !== arr2[i]) {
        // element 
        return 0; // 
      }
    }
    return 1;
  }
  return 0;
}

function max(arr: Array<number>): number {
  let i = 0,
    SIZE = 0;
  let maximum = 0;

  SIZE = arr.length;
  maximum = arr[0];

  for (i = 1; i < SIZE; i++) {
    if (maximum < arr[i]) {
      maximum = arr[i];
    }
  }

  return maximum;
}

function isOnce(arr: Array<number>, num: number): number {
  let i = 0,
    SIZE = 0,
    count = 0;

  SIZE = arr.length;

  for (i = 0; i < SIZE; i++) {
    if (num === arr[i]) {
      count++;
    }
  }

  if (count === 0) {
    return -1;
  } else if (count === 1) {
    return 1;
  } else {
    return 0;
  }
}

