import { Button } from "@mui/material";
import { useContext } from "react";
import { Context } from "../context/context";
import { ContextType } from "../types/context";

const SubmitButton = () => {
  const {
    table,
    columnConditions,
    rowConditions,
    isGameStarted,
    setFinalMessage,
    setEndTime,
  } = useContext(Context) as ContextType;

  const onClick = () => {
    if (checkResult(table, rowConditions, columnConditions)) {
      setFinalMessage("correct answer.");
      setEndTime(new Date().getTime());
    } else {
      setFinalMessage("Wrong answer. Please try again.");
    }
  };
  return (
    <Button
      disabled={!isGameStarted}
      variant="contained"
      style={{
        marginTop: "30px",
        width: "200px",
        height: "50px",
        fontSize: "20pt",
      }}
      onClick={onClick}
    >
      Submit
    </Button>
  );
};

export const checkResult = (
  table: Array<Array<boolean | null>>,
  row: Array<Array<number>>,
  column: Array<Array<number>>
): boolean => {
  const SIZE = row.length;
  let buffer = [];
  let is_true = 0;
  let h = 0,
    w = 0;

  //  
  for (w = 0; w < SIZE; w++) {
    buffer = [];
    is_true = 0; //  
    for (h = 0; h < SIZE; h++) {
      if (table[h][w] === true) {
        //  
        is_true++; //  
      } else {
        //  
        if (is_true !== 0) {
          //    
          buffer.push(is_true); //  
          is_true = 0; // 
        }
      }
    }
    //     
    if (is_true !== 0) {
      buffer.push(is_true); //  
    }
    //    0 (  )
    if (buffer.length === 0) {
      buffer.push(0);
    }
    //   
    if (!equal(column[w], buffer)) {
      return false;
    }
  }

  //  
  for (h = 0; h < SIZE; h++) {
    buffer = [];
    is_true = 0; //  
    for (w = 0; w < SIZE; w++) {
      if (table[h][w] === true) {
        //  
        is_true++; //  
      } else {
        //  
        if (is_true !== 0) {
          //    
          buffer.push(is_true); //  
          is_true = 0; // 
        }
      }
    }
    //     
    if (is_true !== 0) {
      buffer.push(is_true); //  
    }
    //    0 (  )
    if (buffer.length === 0) {
      buffer.push(0);
    }
    //   
    if (!equal(row[h], buffer)) {
      return false;
    }
  }

  //   
  return true;
};

const equal = (arr1: Array<number>, arr2: Array<number>): boolean => {
  if (arr1.length === arr2.length) {
    //  
    const SIZE = arr1.length;
    for (let i = 0; i < SIZE; i++) {
      //  
      if (arr1[i] !== arr2[i]) {
        // element 
        return false;
      }
    }
    return true;
  }
  return false;
};
export default SubmitButton;
